.card {
    height: 90px;
    background-color: peachpuff;
    padding: 10px;
    margin:10px;
    border-radius: 10px;
    text-align: center;
    position: relative;
 }
 .card:hover {
     cursor: pointer;
     box-shadow: 0px 0px 9px 2px #2B24B2;
 }
 
 .card-logo {
     width:65%;
     height: 70px;
     transition: transform .3s ease-out;
 }
 
 .card:hover .card-logo {
     transform: scale(1.15);
 }
 
 .list {
     text-align: left;
     font-size: 0.9rem;
     font-weight: 500;
     list-style-position: outside;
     padding-left: 15px;
 }